import React, { Component } from 'react'
import PropTypes from 'prop-types'
import _ from 'lodash'

import ErrorBoundary from '../app/errorBoundary'

import tastics from '../tastic/tastics'

class Tastic extends Component {
    render () {
        let tastic = this.props.tastic

        if (!tastics[tastic.tasticType]) {
            return (<div className='alert alert-warning'>
                <p>Tastic {tastic.tasticType} not yet implemented.</p>
                <p>Did you just implement it? Please don't forget to register it in the <code>tastic.jsx</code>!</p>
            </div>)
        }
        let Tastic = tastics[tastic.tasticType]

        return (<ErrorBoundary>
            <div className={'tastic ' +
                    (tastic.schema.get('mobile') ? '' : 'e-tastic--hidden-hand ') +
                    (tastic.schema.get('tablet') ? '' : 'e-tastic--hidden-lap ') +
                    (tastic.schema.get('desktop') ? '' : 'e-tastic--hidden-desk ')
                }
                style={{
                    outline: (_.isEqual(tastic.tasticId, this.props.highlight) ? '2px dashed #d73964' : null),
                }}>
                <Tastic tastic={tastic} node={this.props.node} page={this.props.page} data={this.props.data} />
            </div>
        </ErrorBoundary>)
    }
}

Tastic.propTypes = {
    node: PropTypes.object.isRequired,
    page: PropTypes.object.isRequired,
    tastic: PropTypes.object.isRequired,
    data: PropTypes.object.isRequired,
    highlight: PropTypes.any,
}

Tastic.defaultProps = {
}

export default Tastic
