export default (globalState, props) => {
    let product = props.data[props.tastic.configuration.stream]
    if (!product) {
        console.warn('No product could be found in streams')
        return { ...props }
    }

    let selectedVariant = globalState.app.route.get('_variant', 0)
    let variant = product.variants[selectedVariant] || null
    if (!variant) {
        console.warn('Product variants are always required')
        return { ...props }
    }

    // FIXME: Faking this because we do not seem to retrieve it from commerce back-end
    variant.originalPrice = Math.round(variant.price * (product.name.charCodeAt(1) % 4))

    return {
        ...props,
        product: product,
        variant: variant,
        selectedVariant: +selectedVariant,
    }
}
