import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { connect } from 'react-redux'

import Price from '../../../component/price'
import productConnector from '../connector'

class ProductPriceTastic extends Component {
    render () {
        if (!this.props.product || !this.props.variant) {
            return null
        }

        let showPercent = this.props.tastic.schema.get('showPercent')
        let showStrikePrice = this.props.tastic.schema.get('showStrikePrice')
        let isReduced = this.props.variant.originalPrice > this.props.variant.price

        return (<div className='c-page-section'>
            <ul className='c-price-list'>
                <li className='c-price-list__item'>
                    <span
                        className={classnames({
                            'c-price': true,
                            'u-text-l': true,
                            'c-price--highlight': isReduced && (showPercent || showStrikePrice),
                        })}
                    >
                        <Price value={this.props.variant.price} currency={this.props.variant.currency} />
                    </span>
                </li>
                {this.props.variant.originalPrice && this.props.variant.originalPrice > this.props.variant.price ?
                    <Fragment>
                        {showStrikePrice ?
                            <li className='c-price-list__item'>
                                <span className='c-price  c-price--old'><del>
                                    <Price value={this.props.variant.originalPrice} currency={this.props.variant.currency} />
                                </del></span>
                            </li> : null}
                        {showPercent ?
                            <li className='c-price-list__item'>
                                <span className='c-highlight'>
                                    Save {100 - Math.ceil(this.props.variant.price / this.props.variant.originalPrice * 100)} %
                                </span>
                            </li> : null}
                    </Fragment>
                : null}
            </ul>
        </div>)
    }
}

ProductPriceTastic.propTypes = {
    tastic: PropTypes.object.isRequired,
    product: PropTypes.object,
    variant: PropTypes.object,
}

ProductPriceTastic.defaultProps = {
    product: null,
    variant: null,
}

export default connect(productConnector)(ProductPriceTastic)
