import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'

import Link from '../app/link'
import NoImage from '../../layout/noImage.svg'
import RemoteImage from '../remoteImage'
import Price from './price'

class ProductBox extends Component {
    render () {
        let product = this.props.product
        let variant = this.props.variant || product.variants[0]

        // FIXME: Faking this because we do not seem to retrieve it from commerce back-end
        variant.originalPrice = Math.round(variant.price * (product.name.charCodeAt(1) % 4))

        let isReduced = variant.originalPrice > variant.price
        return (<article className='c-teaser  o-block' itemScope itemType='http://schema.org/Product'>
            <Link itemProp='url' className='c-teaser__full-link'
                route='Frontastic.Frontend.Master.Product.view'
                params={{ id: product.productId, slug: product.slug }}>

                <figure className='c-teaser__figure  o-block__figure'>
                    <RemoteImage
                        className='c-teaser__image'
                        url={variant.images[0] || NoImage}
                        alt={product.name}
                        cropRatio='1:1'
                        itemProp='image'
                        options={{ crop: 'pad', background: 'white' }}
                    />
                </figure>
                <div className='o-block__body'>
                    <div className='c-teaser__body'>
                        <div className='c-teaser__caption'>
                            <h3 className='c-teaser__title  c-heading-teaser' itemProp='name'>
                                {product.name}
                            </h3>
                            {variant.attributes.designer ?
                                <div className='c-heading-teaser-subheading'>{variant.attributes.designer}</div>
                            : null}
                        </div>
                        <div className='c-teaser__prices' itemScope itemType='http://schema.org/Offer'>
                            <span itemProp='price'
                                className={classnames({
                                    'c-teaser__price': true,
                                    'c-price': true,
                                    'c-price--highlight': isReduced && (this.props.showPercent || this.props.showStrikePrice),
                                })}
                            >
                                <Price value={variant.price} currency={variant.currency} />
                            </span>
                            {isReduced && this.props.showStrikePrice ?
                                <span className='c-price  c-price--old'><del>
                                    <Price value={variant.originalPrice} currency={variant.currency} />
                                </del></span> : null}
                            {isReduced && this.props.showPercent ?
                                <span className='c-highlight'>
                                    -&nbsp;{100 - Math.ceil(variant.price / variant.originalPrice * 100)}%
                                </span> : null}
                        </div>
                    </div>
                </div>
            </Link>
        </article>)
    }
}

ProductBox.propTypes = {
    product: PropTypes.object.isRequired,
    variant: PropTypes.object,
    showStrikePrice: PropTypes.bool,
    showPercent: PropTypes.bool,
}

ProductBox.defaultProps = {
    showStrikePrice: true,
    showPercent: true,
    variant: null,
}

export default ProductBox
