import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import _ from 'lodash'

import iconBag from '../../../icons/icomoon_icons/SVG/bag.svg'
import iconCross from '../../../icons/icomoon_icons/SVG/cross.svg'
import Basket from '../../domain/basket'
import Price from '../../component/price'

class MiniBasket extends Component {
    constructor (props) {
        super(props)

        this.state = {
            showBasket: false,
        }
    }

    render () {
        let basketItems = this.props.basket.getItems()

        return (<Fragment>
            <button
                className='c-button c-navbar__button c-cart-link'
                aria-label='Mein Warenkorb'
                onClick={() => {
                    this.setState({ showBasket: true })
                }} >
                <img className='o-icon' data-icon='cart' data-icon-size='base' src={iconBag} alt='Cart' />
                <span className='c-cart-link__badge c-badge c-badge--tiny'>
                    {this.props.basket.getProductCount()}
                </span>
            </button>

            <div className={'c-overlay' + (this.state.showBasket ? ' is-visible' : '')}>
                <nav className='c-sequential-nav' data-ft-sequential-nav=''>
                    <div className='c-sequential-nav__panel c-sequential-nav__panel--level-1'>
                        <div className='c-sequential-nav__header'>
                            <h3 className='c-sequential-nav__title'>Mein Warenkorb</h3>
                            <button className='c-sequential-nav__primary-action c-button' onClick={() => {
                                this.setState({ showBasket: false })
                            }}>
                                <img className='o-icon' data-icon='cross' data-icon-size='base' src={iconCross} alt='Close' />
                            </button>
                        </div>
                        <div className='c-sequential-nav__content'>
                            <table className='o-table o-table--small'>
                                <tbody>
                                    {!basketItems.length ?
                                        <tr><td>Nothing in here</td></tr> :
                                        _.map(basketItems, (item) => {
                                            return (<tr key={item.product.productId + '_' + item.variant.id}>
                                                <td>
                                                    {item.product.name}
                                                </td>
                                                <td align='right'>
                                                    {item.count}
                                                </td>
                                                <td align='right'>
                                                    <Price value={item.variant.price} currency={item.variant.currency} />
                                                </td>
                                                <td align='right'>
                                                    <Price value={item.variant.price * item.count} currency={item.variant.currency} />
                                                </td>
                                            </tr>)
                                        })
                                    }
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <th colSpan='3' align='left'>
                                            Sum
                                        </th>
                                        <th align='right'>
                                            <Price currency='EUR' value={_.sumBy(basketItems, (item) => {
                                                return item.variant.price * item.count
                                            })} />
                                        </th>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                        <div className='c-sequential-nav__footer'>
                            <button disabled className='c-sequential-nav__primary-action c-button c-button--primary' onClick={() => {
                                this.setState({ showBasket: false })
                            }}>
                                Checkout
                            </button>
                        </div>
                    </div>
                </nav>
            </div>
        </Fragment>)
    }
}

MiniBasket.propTypes = {
    basket: PropTypes.instanceOf(Basket).isRequired,
}

MiniBasket.defaultProps = {}

export default connect(
    (globalState, props) => {
        return {
            basket: globalState.basket.basket,
        }
    }
)(MiniBasket)
