import _ from 'lodash'

import Basket from './basket'

/**
 * Loader classes like this consilidate all loading monitors for a domain
 * concept.
 *
 * They define one (or multiple) load() methods which execute an AJAX call
 * through the Api. You may implement parameter remapping and cache checks in
 * here. For the loaded date there should be a dispatched action through Redux.
 *
 * The Loader also defines a (static) method which handles its own actions and
 * applies the corresponding changes to the global store state.
 */
let BasketLoader = function (store, api) {
    this.store = store
    this.api = api

    this.add = (product, variant, count) => {
        let basketItems = this.store.getState().basket.basket.items

        let foundItem = _.find(basketItems, (basketItem) => {
            return (basketItem.product.productId === product.productId &&
                basketItem.variant.id === variant.id)
        })

        if (foundItem) {
            foundItem.count += count
        } else {
            basketItems.push({
                product: product,
                variant: variant,
                count: count,
            })
        }

        this.store.dispatch({
            type: 'Domain.Basket.success',
            data: {
                items: basketItems,
            },
        })
    }
}

const initialGlobalState = {
    basket: new Basket(),
}

BasketLoader.handleAction = (globalState = initialGlobalState, action) => {
    switch (action.type) {
    case 'FRONTASTIC_ROUTE':
        // No purging, fake basket is persistent
        return globalState

    case 'Domain.Basket.success':
        return {
            ...globalState,
            basket: new Basket(action.data.items),
        }

    default:
    // Do nothing for other actions
    }

    return globalState
}

export default BasketLoader
