import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import app from '../../app/app'
import Link from '../../app/link'
import Entity from '../../app/entity'

import logo from '../../../layout/logo.svg'

import MiniBasket from './miniBasket'
import Sequential from './sequential'
import SvgIcon from '../../component/svgIcon'

class NavigationTastic extends Component {
    constructor (props) {
        super(props)

        this.state = {
            showNavigation: false,
        }
    }

    componentDidMount () {
        // @TODO: This is supposed to be already fetched on server side.
        let root = this.props.tastic.schema.get('root')
        if (root) {
            app.getLoader('node').loadTree({
                root: root,
                depth: this.props.tastic.schema.get('depth'),
            })
        }
    }

    render () {
        let customLogo = this.props.tastic.schema.get('logo') || null

        return (<Fragment>
            <div className='c-navbar'>
                <button
                    className='c-button'
                    disabled={!this.props.tree.isComplete() || !this.props.tree.data.children.length}
                    onClick={() => {
                        this.setState({ showNavigation: true })
                    }}
                >
                    <SvgIcon icon='menu' />
                </button>

                <Link path='/' className='c-logo c-navbar__logo'>
                    {customLogo && customLogo.media ?
                        <img className='c-navbar__logo-image' src={customLogo.media.file} alt='Frontastic' /> :
                        <img className='c-navbar__logo-image' src={logo} alt='Frontastic' />}
                </Link>

                {this.props.tastic.schema.get('showSearch') ?
                    <button className='c-button c-navbar__button' disabled>
                        <SvgIcon icon='search' />
                    </button> : null}

                {this.props.tastic.schema.get('showBasket') ? <MiniBasket /> : null}

                {this.props.tree.isComplete() && this.props.tree.data.children.length ?
                    <div className={'c-overlay' + (this.state.showNavigation ? ' is-visible' : '')}>
                        <nav className='c-sequential-nav' data-ft-sequential-nav=''>
                            <Sequential
                                root={this.props.tree.data}
                                onClose={() => {
                                    this.setState({ showNavigation: false })
                                }} />
                        </nav>
                    </div> : null}
            </div>
            <div className='c-navbar__spacing' />
        </Fragment>)
    }
}

NavigationTastic.propTypes = {
    tree: PropTypes.object.isRequired,
    data: PropTypes.object.isRequired,
    tastic: PropTypes.object.isRequired,
}

NavigationTastic.defaultProps = {
}

export default connect(
    (globalState, props) => {
        return {
            ...props,
            tree: globalState.node.trees[props.tastic.configuration.root || 'root'] || new Entity(),
        }
    }
)(NavigationTastic)
